html {
  box-sizing: border-box;
  font-size: 100%;
  @media #{$mq-1000-up} {
    font-size: 1vw;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: $primary;
}
a:hover {
  color: $primary;
}

#map {
  margin-top: 4.8rem;
  height: calc(100vh - 4.8rem);
}

body {
  @include roboto-regular;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  background-color: $white;
  color: $black;
  line-height: 1.3;
}

dl,
ol,
ul {
  margin-top: auto;
  margin-bottom: auto;
}

.container {
  max-width: 100%;
  width: auto;
  margin: 0 4.2rem;
  @include rmax(992px) {
    margin: 0 15px;
  }
}

.row > * {
  padding-right: 1.125rem;
  padding-left: 1.125rem;
}

.row {
  margin-right: -1.125rem;
  margin-left: -1.125rem;
}

.content {
  @media #{$mq-1000-up} {
    flex: 3;
  }
}

aside {
  @media #{$mq-1000-up} {
    flex: 1;
  }
}
.white-text {
  color: $white;
}
.pink-text {
  color: $darkpink;
}
.ml-1 {
  margin-left: 1rem;
}
.ml-2 {
  margin-left: 2rem;
}
.ml-3 {
  margin-left: 3rem;
}

.pt-1 {
  padding-top: 1rem;
}

@for $i from 1 through 5 {
  .pdg-t-#{$i} {
    padding-top: #{$i}rem;
  }
}

@for $i from 1 through 5 {
  .pdg-b-#{$i} {
    padding-bottom: #{$i}rem;
  }
}

@for $i from 1 through 5 {
  .mgn-b-#{$i} {
    margin-bottom: #{$i}rem;
  }
}

@include rmax(992px) {
  @for $i from 1 through 5 {
    .mgn-b-#{$i} {
      margin-bottom: #{"calc(" $i "* 0.2)"}rem !important;
    }
  }
}

.page-wrap {
  width: auto;
  height: auto;
  padding-top: 4.5rem;
}

.top-border {
  border-top: 1px solid #ccc;
}

.mtb-1 {
  margin: 1rem auto;
}
.mtb-2 {
  margin: 2rem auto;
}
.mtb-3 {
  margin: 3rem auto;
}

.mlr-1 {
  margin: 0 1rem;
}
.mlr-2 {
  margin: 0 1rem;
}
.mlr-3 {
  margin: 0 1rem;
}

.ptb-1 {
  padding: 1rem 0;
}
.ptb-2 {
  padding: 2rem 0;
}
.ptb-3 {
  padding: 3rem 0;
}

.sp {
  padding: $secPadding 0;
  @include rmax(767px) {
    padding: 3rem 0;
  }
}

.spt {
  padding-top: $secPadding;
}

.spb {
  padding-bottom: $secPadding;
}

.spacer-s1 {
  width: auto;
  padding: auto;
  display: block;
  height: 1rem;
}

.spacer-s2 {
  width: auto;
  padding: auto;
  display: block;
  height: 2rem;
}
.spacer-s3 {
  width: auto;
  padding: auto;
  display: block;
  height: 3rem;
}
.spacer-s4 {
  width: auto;
  padding: auto;
  display: block;
  height: 4rem;
}

.spacer-s6 {
  width: auto;
  padding: auto;
  display: block;
  height: 6rem;
}

.spacer-s8 {
  width: auto;
  padding: auto;
  display: block;
  height: 8rem;
}
.bg-light-blue {
  background-color: $bglightblue;
}

.bg-dark-blue {
  background-color: $bgblue;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
  li {
    margin: 0;
  }
}
.icon-list {
  margin: 1rem 0;
  li {
    @include flx;
    padding-top: 1rem;
    font-size: 1.125rem;
    line-height: 1.5rem;
    @include roboto-regular;
    .iwrap {
      width: 1.9375rem;
      height: 1.9375rem;
      @include flx-ctr;
      background-color: $primary;
      margin-right: 0.5rem;
      border-radius: 0.3rem;
    }
  }
  i {
    padding: 0.3rem;
    color: #fff;
    font-size: 1.2rem;
  }
}
.count-list {
  margin: 3rem 0;
  li {
    position: relative;
    padding-left: 0.8rem;
    font-size: 1.5625rem;
    margin: 0.5rem 0;
    color: #0e2645;
    @include roboto-slab-medium;
    &:before {
      position: absolute;
      content: " ";
      top: 0.3rem;
      left: 0;
      width: 0.3rem;
      height: 1.3rem;
    }
    &:nth-child(1) {
      &:before {
        background-color: #e9ba00;
      }
    }
    &:nth-child(2) {
      &:before {
        background-color: #69bbd9;
      }
    }
    &:nth-child(3) {
      &:before {
        background-color: #924f8d;
      }
    }
  }
}

.dot-list {
  li {
    position: relative;
    padding-left: 1rem;
    font-size: 1.12rem;
    margin: 0.5rem 0;
    color: $bgblue;
    @include roboto-regular;
    &:before {
      position: absolute;
      content: " ";
      top: 0.5rem;
      left: 0;
      width: 4px;
      height: 4px;
      border-radius: 2px;
      background-color: $bgblue;
    }
  }
}

.link-list {
  li {
    margin: 0.625rem 0;
    @include roboto-regular;
    a {
      color: #000;
      display: inline-flex;
    }
  }
}

button,
.btn {
  background: #e83675;
  border: 1px solid #bc155c;
  outline: 0;
  padding: 0.625rem 1.6rem;
  color: #fff;
  text-decoration: none;
  @include roboto-regular;
  font-size: 1rem;
  line-height: 1.3125rem;
  border-radius: 0.3rem;
  margin: auto;
  &:hover {
    color: #fff;
    background: darken($color: #e83675, $amount: 10%);
  }
}

.fullwidth-btn {
  width: 100%;
  height: auto;
  display: block;
  border-radius: 0.4rem;
  margin-top: 1.5rem;
  text-align: center;
  padding: 0.7rem 0;
  @include roboto-regular;
  font-size: 1.17rem;
  line-height: 1.5rem;
  color: $white;
  &:hover {
    color: $white;
  }
}
.gd-btn-yellow {
  background: rgb(233, 186, 0);
  background: linear-gradient(
    90deg,
    rgba(233, 186, 0, 1) 0%,
    rgba(231, 134, 50, 1) 100%
  );
  &:hover {
    background: linear-gradient(
      90deg,
      rgb(187, 150, 2) 0%,
      rgb(207, 118, 40) 100%
    );
  }
}

.gd-btn-lila {
  background: rgb(146, 79, 141);
  background: linear-gradient(
    90deg,
    rgba(146, 79, 141, 1) 0%,
    rgba(21, 66, 129, 1) 100%
  );
  &:hover {
    background: linear-gradient(
      90deg,
      rgb(138, 65, 133) 0%,
      rgb(17, 55, 109) 100%
    );
  }
}

.gd-btn-blue {
  background: rgb(85, 180, 210);
  background: linear-gradient(
    90deg,
    rgba(85, 180, 210, 1) 0%,
    rgba(21, 66, 129, 1) 100%
  );
  &:hover {
    background: linear-gradient(
      90deg,
      rgb(66, 148, 173) 0%,
      rgb(15, 50, 100) 100%
    );
  }
}

.spl-btn {
  padding: 0.625rem 2.125rem;
  border: transparent;
  border-width: 1px;
  border-style: solid;
  color: #fff;
  text-decoration: none;
  @include roboto-regular;
  font-size: 1rem;
  line-height: 1.3125rem;
  border-radius: 0.3rem;
  margin: auto;
  margin-left: 1rem;
  @include rmax(767px) {
    display: block;
    margin: 0;
    margin-top: 1rem;
    padding: 0.5rem 3rem;
  }
}
.btn-blue {
  background-color: #154281;
  border-color: #55b4d2;
  &:hover {
    background-color: darken($color: #154281, $amount: 10%);
  }
}
.btn-brown {
  background-color: #eb782d;
  border-color: #ebb02c;
  &:hover {
    background-color: darken($color: #eb782d, $amount: 10%);
  }
}
.form-wrapper {
  @include flx-btwn;
  background: #fff;
  border-radius: 5px;
  overflow: hidden;
  height: 2.6rem;
  @include rmax(767px) {
    flex-direction: column;
    align-items: inherit;
  }
}
.input-style {
  padding: 0.625rem;
  color: $textblue;
  text-decoration: none;
  font-size: 1rem;
  line-height: 1.3125rem;
  flex: 1 1 auto;
  width: 1%;
  border: none;
  outline: 0 !important;
  @include rmax(767px) {
    border-radius: 0.3rem;
    width: 100%;
  }
}

.ctype-a {
  margin-top: 2rem;
  border-radius: 0.3rem;
  box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.14);
  -webkit-box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.14);
  -moz-box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.14);
  border: 2px solid transparent;
  transition: all 0.1s;
  &:hover {
    border: 2px solid #e78632;
    transform: scale(1.04);
  }
  @include rminmax(768px, 992px) {
    margin-bottom: 2rem;
  }
  @include rmax(767px) {
    margin-top: 4rem;
  }

  .card-header {
    background-color: #fff;
    border-bottom: 0;
    padding: 0;
    border-radius: 0.6rem;
    position: relative;
    img {
      margin-top: -2.7rem;
      width: calc(100% + 1.6rem);
      margin-left: -0.8rem;
    }
    .sub-img {
      width: 5.85rem;
      height: 5.85rem;
      border-radius: 0.4rem;
      position: absolute;
      right: 0.1rem;
      bottom: 2rem;
      img {
        margin-top: 0;
        width: 100%;
        border: 0.25rem solid #fff;
        background: #fff;
        border-radius: 0.3rem;
      }
    }
  }
  .card-body {
    padding: 1.2rem;
    padding-top: 0;

    .card-title {
      @include roboto-light;
      font-size: 1.125rem;
      line-height: 1.5rem;
      color: $textblue;
    }
    .item-value-row {
      @include flx-btwn;
      @include roboto-light;
      font-size: 0.875rem;
      line-height: 0.875rem;
      margin-bottom: 0.5rem;
      .ivr-item {
        color: $textblue;
      }
      .ivr-value {
        color: $primary;
        img {
          width: 1.2rem;
          height: 1.2rem;
        }
      }
    }
  }
}

.ctype-flexy {
