.ctype-netcard {
  margin-top: 2rem;
  border-radius: 0.3rem;
  box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.14);
  -webkit-box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.14);
  -moz-box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.14);
  border: 2px solid transparent;
