.site-header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  margin-right: auto;
  background-color: #fff;
  z-index: 1000;
  transition: all 0.2s;
  .head-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.125rem 0;
    position: relative;
    background: #fff;
    .logo-area {
      width: 8.875rem;
      height: auto;
      img {
        width: 100%;
        height: auto;
      }
    }
    .nav-area {
      ul {
        display: flex;
        li {
          padding-left: 3.3rem;
          @include rmax(767px) {
            padding-left: 0;
          }
        }
        a {
          @include roboto-medium;
          font-size: 1.17rem;
          color: #0e2645;
          padding: 1rem 0;
          &:hover {
            color: $primary;
          }
        }
      }
      @include rmax(992px) {
        position: absolute;
        top: 72px;
        background: #fff;
        width: auto;
        left: -2rem;
        right: -2rem;
        padding: 0;
        transform: scale(0);
        transform: translateY(-100%);
        z-index: -1;
        transition: all 0.2s;
        padding: 30px 0;
        border-bottom: 1px solid #f4f4f4;
        border-top: 1px solid #f4f4f4;

        ul {
          flex-direction: column;
          li {
            padding: 10px;
            text-align: center;
            &:last-child {
              display: none;
            }
          }
        }
      }
    }
    // .mobile-toggler {
    //   display: none;
    //   @include rmax(992px) {
    //     display: block;
    //     font-size: 25px;
    //     cursor: pointer;
    //   }
    //   &:hover {
    //     color: $primary;
    //   }
    // }
    .mob-only-area {
      display: none;
      flex-direction: row;
      @include rmax(992px) {
        display: flex;
      }
      .mobile-spl-menu {
        margin-right: 1rem;
        font-size: 1.2rem;
        a {
          color: $black;
          &:hover {
            color: $primary;
          }
        }
      }
    }
    .mobile-toggler {
      font-size: 30px;
      cursor: pointer;
      display: none;
      .btn-line {
        width: 28px;
        height: 3px;
        margin: 0 0 5px 0;
        background: $black;
        transition: all 0.2s ease-out;
      }
      &.close {
        transform: rotate(180deg);
        .btn-line {
          &:nth-child(1) {
            transform: rotate(45deg) translate(11px, 9px);
          }
          &:nth-child(2) {
            opacity: 0;
          }
          &:nth-child(3) {
            transform: rotate(-45deg) translate(3px, 0px);
          }
        }
      }
      @include rmax(992px) {
        display: block;
      }
    }
    .nav-scale {
      transform: translateY(0);
    }
  }
}

.footer-wrapper {
  width: auto;
  height: auto;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  min-height: 25rem;
  padding-top: 5rem;
  font-size: 0.875rem;
  line-height: 1.1875rem;
  @include rmax(992px) {
    font-size: 16px;
    line-height: 20px;
  }
  .footer-up {
    .footer-link-title {
      @include roboto-medium;
    }
    a {
      color: #000;
      &:hover {
        color: $darkpink;
      }
    }
  }

  .footer-credits {
    .footer-left {
      @include rmax(767px) {
        margin-bottom: 30px;
        text-align: center;
        line-height: 1.5;
      }
    }
    .footer-right {
      @include flx;
      @include roboto-medium;
      justify-content: flex-end;
      @include rmax(767px) {
        justify-content: center;
      }
      .lang-list {
        margin-right: 2rem;
        li {
          display: flex;
          align-items: center;
        }
        i {
          padding-right: 1rem;
        }
        a {
          color: #000;
          &:hover {
            color: $darkpink;
          }
        }
      }
      ul {
        @include flx;
        i {
          font-size: 1.375rem;
          padding-left: 0.75rem;
        }
        a {
          &:hover {
            color: $darkpink;
          }
        }
      }
    }
    .footer-left {
      a {
        color: #000;
        &:hover {
          color: $darkpink;
        }
      }
    }
  }
}

.banner {
  width: 100%;
  height: 100vh;
  position: relative;
  .bannerimg {
    width: 100%;
    height: 100vh;
    object-fit: cover;
    object-position: center;
    position: absolute;
  }
  .banner-overlay {
    position: absolute;
    @include container-padding;
    @include rmax(992px) {
      padding: 0 30px;
    }
    color: #fff;
    width: 100%;
    height: 100vh;
    background-color: rgba($bgblue, 0.7);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    .title-wrapper {
      width: 43%;
      height: auto;
      margin-top: 10vh;
      h1 {
        margin-bottom: 0.5rem;
      }
      @include rmax(992px) {
        width: 100%;
        margin-top: 90px;
      }
    }
    .subscription-form {
      width: 100%;
      max-width: 20.625rem;
      height: auto;
      border-radius: 0.3rem;
      overflow: hidden;
      @include rmax(992px) {
        width: 100%;
        background-color: transparent;
      }
      button {
        width: auto;
        padding: 0.625rem;
        margin: 0;
        border: 1px solid #e83675;
        @include rmax(767px) {
          width: 100%;
          margin-top: 20px;
        }
      }
    }
    .pay-icons {
      display: flex;
      margin-top: 4rem;
      @include rmax(992px) {
        margin-top: 2rem;
      }
      li {
        padding-right: 0.235rem;
        font-size: 1.3rem;
        @include rmax(992px) {
          font-size: 16px;
        }
      }
    }
  }
}
.social {
  width: 100%;
  height: auto;
  background: url("../../images/social-icons-zigzag.jpg");
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
  background-size: 60%;
  color: $textblue;
  @include rmax(992px) {
    background: transparent;
  }
  p {
    font-size: 1.125rem;
    line-height: 1.5rem;
    @include roboto-regular;
    margin-top: 3rem;
  }
  a {
    font-size: 1.125rem;
    line-height: 1.375rem;
    @include roboto-regular;
    color: $primary;
    &:hover {
      color: $darkpink;
    }
  }
}
.overview {
  width: 100%;
  height: auto;
  background: url("../../images/overview.jpg");
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
  background-size: 60%;
  @include rmax(992px) {
    background: transparent;
  }
  a {
    color: $darkpink;
    &:hover {
      color: $primary;
    }
  }
  a.btn {
    color: #fff;
  }
}

.heading-btn-section {
  display: flex;
  align-items: flex-end;
  margin-bottom: 0.75rem;
  h5 {
    margin-bottom: 0;
    color: #0e2645;
  }
  @include rminmax(768px, 992px) {
    h5 {
      line-height: 1.3;
    }
  }
  @include rmax(767px) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.specs {
  width: 100%;
  height: auto;
  text-align: center;
  background: rgb(21, 66, 129);
  background: linear-gradient(
    90deg,
    rgba(21, 66, 129, 1) 0%,
    rgba(14, 38, 69, 1) 100%
  );
  .row {
    margin-left: -1.35rem;
    margin-right: -1.35rem;
    .lgap {
      padding-left: 1.35rem;
      padding-right: 1.35rem;
    }
  }
}

.details-card {
  width: auto;
  height: 100%;
  padding: 1.875rem;
  background-color: $white;
  border-radius: 0.4rem;
  text-align: left;
  color: $textblue;
  @include rmax(992px) {
    height: auto;
    margin-top: 2rem;
  }
  .dc-head {
    margin-bottom: 3rem;
    img {
      height: 3rem;
      width: auto;
      @include rmax(992px) {
        height: 2rem;
      }
    }
  }
  .dc-title {
    @include roboto-bold;
    font-size: 1.125rem;
    line-height: 1.375rem;
    margin-bottom: 1rem;
  }
}

@include rmax(767px) {
  aside {
    margin-bottom: 2.5rem;
  }
}

.item-map-list-container {
  width: 100%;
  height: auto;
  display: flex;
  .item-list-area {
    width: auto;
    margin-left: -1rem;
    margin-right: -1rem;
    .items-head {
      @include roboto-medium;
      font-size: 0.875rem;
      a {
        color: $darkpink;
        &:hover {
          color: $primary;
        }
      }
    }
    .map-switcher {
      a {
        color: $black;
      }
      i {
        color: $black;
      }
    }
    .list-head {
      @include flx-btwn;
      margin: 1rem;
      margin-top: 2rem;
    }
    .mid-title {
      width: auto;
      margin: 1rem;
      margin-bottom: 0;
      &.store {
        margin-bottom: -1rem;
      }
      h5 {
        margin-bottom: 0.3rem;
        color: $textblue;
      }
    }
    .ntwk-list {
      display: flex;
      width: auto;
      margin-left: 1rem;
      margin-right: 1rem;
      margin-bottom: 2rem;
    }
    .item-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .box-item {
        width: 20rem;
        height: auto;
        margin: 1rem;
      }
    }
    .item-pagination {
      margin: auto;
      width: auto;
      text-align: center;
      margin: 2.5rem 0;
      .pagination {
        justify-content: center;
        margin-bottom: 1rem;
        .page-item {
          margin: 0 0.5rem;
          border-radius: 50%;
          &.active {
            .page-link {
              color: $white;
              background-color: $darkpink;
              border-color: $darkpink;
            }
          }
          .page-link {
            border-radius: 50%;
            color: $black;
          }
          &:first-child {
            .page-link {
              border-radius: 50%;
            }
          }
          &:last-child {
            .page-link {
              border-radius: 50%;
            }
          }
        }
      }
      .item-pagination-counts {
        width: 100%;
        text-align: center;
      }
    }
  }
}

.item-map-list-container {
  .item-list-area {
    &.map-wrap-view {
      width: 50%;
      .list-head {
        @include flx-btwn;
        margin: 1rem;
        margin-top: 2rem;
      }
      .mid-title {
        &.store {
          margin-bottom: -1.8rem;
        }
      }
      .item-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        .box-item {
          width: 100%;
          height: auto;
          margin: 1rem;
          .ctype-flexy {
            display: flex;
            flex-direction: row;
            .card-title {
              @include roboto-regular;
            }

            .card-header {
              width: 45%;
              img {
                margin-left: 0;
                margin-top: -2rem;
                margin-bottom: -3rem;
                margin-left: -1.5rem;
                width: 112%;
                height: 15.5rem;
                object-fit: cover;
                object-position: center;
              }
            }
            .card-body {
              padding: 1.2rem 1.2rem 0.5rem 1rem;
              .inner-flex {
                height: 100%;
                .flex-up-row {
                  width: 100%;
                  height: auto;
                }
                .flexy-bottom-row {
                  justify-content: space-between;
                  .fbtm-txt {
                    display: block;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

.map-view {
  width: 0;
  height: 0;
  .map-close-btn {
    display: none;
  }
  &.map-wrap-view {
    height: 100vh;
    background-color: $white;
    position: fixed;
    width: 50%;
    right: 0;
    top: 0;
    .map-close-btn {
      width: 30px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      text-align: center;
      background: $white;
      color: $black;
      position: absolute;
      left: 1rem;
      top: 6rem;
      font-size: 1.2rem;
      border-radius: 50%;
    }
  }
}

footer {
  z-index: 2;
  position: relative;
  background: #fff;
}

.filter-search-block {
  display: flex;
  .input-group {
    border: 1px solid #cbd9e2;
    border-radius: 5px;
    overflow: hidden;
    margin: 0 0.5rem;
    &.search {
      box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.29);
      -webkit-box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.29);
      -moz-box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.29);
      i {
        color: $darkpink;
      }
    }
    input {
      border: none;
      padding: 0.5rem;
    }
    button {
      border: none;
      padding: 0.5rem;
      background: transparent;
    }
  }
  .filter {
    border: 1px solid #cbd9e2;
    background: transparent;
    border-radius: 5px;
    overflow: hidden;
    margin: 0 0.5rem;
    padding: 0.7rem;
    color: #0e2645;
    display: flex;
    align-items: center;
    i {
      color: #cbd9e2;
      padding-right: 0.5rem;
    }
    &.active {
      i {
        color: #000;
      }
    }
  }
}

.modalfilter {
  border-radius: 0.625rem;
  color: $textblue;
  .modal-dialog {
    max-width: 50rem;
    position: relative;

    .modal-title {
      width: 100%;
      text-align: center;
    }
    .btn-close {
      position: absolute;
      border: 1px solid #ccc;
      border-radius: 50%;
      box-shadow: 1px 2px 4px rgb(0, 0, 0, 0.2);
      &:hover {
        background: url("../../images/icons/times-solid.svg");
        background-repeat: no-repeat;
        background-position: center;
      }
    }
    .modal-body {
      background-color: #e5ecf0;
      padding: 0;
      padding-bottom: 1.5rem;
      .filter-switch {
        background-color: #c3d4dd;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        padding: 2rem;
        padding-bottom: 1rem;
        .switch-desc {
          @include roboto-regular;
          .switch-title {
            font-size: 1rem;
            margin-bottom: 0.5rem;
          }
          .switch-details {
            font-size: 0.875rem;
            margin-bottom: 0;
          }
        }
        .form-check-input:checked {
          background-color: #4ac34f;
          border-color: #379f3b;
        }
      }
      .filter-search {
        margin: 1rem 2rem;
        border-bottom: 1px solid #ccc;
        padding-bottom: 0.5rem;
        .input-group-text {
          background-color: white;
          border-right: 0;
          color: $primary;
        }
        input.form-control {
          border-left: 0;
        }
      }
      .filter-subtitle {
        margin: 0 2rem;
        padding-bottom: 0.5rem;
        h5 {
          margin-bottom: 0;
          line-height: 1;
        }
      }
      .filter-check-group {
        margin: 1rem 2rem;
        border-bottom: 1px solid #ccc;
        padding-bottom: 0.5rem;
        .check-title {
          padding-bottom: 0.5rem;
        }
      }
    }
    .modal-footer {
      justify-content: space-between;
      padding: 0.5rem;
      a {
        color: $textblue;
        text-decoration: underline;
      }
      button {
        margin: 0;
        background-color: $darkpink;
        &:hover {
          background-color: $primary;
        }
      }
    }
  }
}
