  text-decoration: underline;
}
.modalfilter .modal-dialog .modal-footer button {
  margin: 0;
  background-color: #c02169;
}
.modalfilter .modal-dialog .modal-footer button:hover {
  background-color: #e83675;
}

h1 {
  font-family: "Roboto Slab", "Open Sans", sans-serif;
  font-weight: bold;
  font-style: normal;
  font-size: 5rem;
  line-height: 5.5rem;
  margin-top: 0px;
  margin-bottom: 1.5rem;
}

h2 {
  font-family: "Roboto Slab", "Open Sans", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 2.5rem;
  margin-top: 0px;
  margin-bottom: 1.5rem;
}

h3 {
  font-family: "Roboto", "Open Sans", sans-serif;
  font-weight: 900;
  font-style: normal;
  font-size: 2.56rem;
  margin-top: 0px;
  margin-bottom: 1.5rem;
}

h4 {
  font-family: "Roboto Slab", "Open Sans", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 1.875rem;
  margin-top: 0px;
  margin-bottom: 1.5rem;
}

h5 {
  font-family: "Roboto Slab", "Open Sans", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 1.5625rem;
  line-height: 2.0625;
  margin-top: 0px;
  margin-bottom: 1.5rem;
}

h6 {
  font-family: "Roboto", "Open Sans", sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 1.5625rem;
  line-height: 2.5rem;
  margin-top: 0px;
  margin-bottom: 1.5rem;
}

p {
  font-family: "Roboto", "Open Sans", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 1.125rem;
  line-height: 1.375rem;
  margin-top: 0px;
  margin-bottom: 1.5rem;
}

@media only screen and (max-width: 767px) {
  h1,
h2,
h3,
h4,
h5,
h6 {
    line-height: 1.3;
  }
}
@media only screen and (max-width: 767px) {
  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 2rem;
  }

  h3 {
    font-size: 2rem;
  }

  h4 {
    font-size: 2rem;
  }

  h5 {
    font-size: 1.4rem;
  }

  h6 {
    font-size: 1.3rem;
  }
}
.heading-spl-a {
  font-family: "Roboto Slab", "Open Sans", sans-serif;
  font-weight: bold;
  font-style: normal;
  font-size: 2.5rem;
  margin-top: 0px;
  margin-bottom: 1.5rem;
}

.font-spl {
  font-size: 1.25rem;
}
@media only screen and (min-width: 1000px) {
  .font-spl {
    font-size: 2rem;
  }
}